main {
  padding-top: 78px; }

.btn {
  font-size: 14px;
  line-height: 1.2em;
  padding: 14px 24px;
  border-radius: 10px;
  font-weight: 500;
  transition: all .3s ease;
  &:hover {
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: none; }
  &.disabled,
  &:disabled {
    opacity: 0.5; }
  .icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    &:first-child {
      margin-right: 8px; }
    &:last-child {
      margin-left: 8px; } }
  span {
    display: inline-block; }

  // BTN PRIMARY
  &-primary {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:hover {
    color: #FFF;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:focus, &-primary.focus {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%);
    box-shadow: none; }
  &-primary.disabled, &-primary:disabled {
    color: #838683;
    background-color: #BDBFBD;
    border-color: #BDBFBD;
    cursor: inherit; }
  &-primary:not(:disabled):not(.disabled):active,
  &-primary:not(:disabled):not(.disabled).active,
  .show > &-primary.dropdown-toggle {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:not(:disabled):not(.disabled).active:focus,
  &-primary:not(:disabled):not(.disabled):active:focus,
  .show > &-primary.dropdown-toggle:focus {
    box-shadow: none; }

  // BTN border
  &-border {
    color: $primary;
    background-color: transparent;
    background: transparent;
    border-color: $primary; }
  &-border:hover {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-border:focus, &-border.focus {
    color: #FFF;
    background-color: $primary;
    border-color: $primary;
    box-shadow: none; }
  &-border.disabled, &-border:disabled {
    color: #FFF;
    background-color: $border;
    border-color: $border; }
  &-border:not(:disabled):not(.disabled):active,
  &-border:not(:disabled):not(.disabled).active,
  .show > &-border.dropdown-toggle {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-border:not(:disabled):not(.disabled).active:focus,
  &-border:not(:disabled):not(.disabled):active:focus,
  .show > &-border.dropdown-toggle:focus {
    box-shadow: none; }

  // BTN Link
  &-link {
    color: $primary; }
  &-link:hover {
    color: $primary;
    opacity: .8; }

  // BTN Delete
  &-delete {
    color: #DB3838;
    padding: 0;
    .ic {
      display: inline-block;
      vertical-align: middle;
      width: 20px;
      height: 20px;
      margin-right: 12px; }
    &:hover {
      color: #DB3838;
      opacity: .8; } }
  // BTN Small
  &-small {
    padding: 8px;
    min-width: 0 !important;
    width: auto; }
  &-long {
    min-width: 190px;
    padding: 14px 44px; } }

