        &__footer {}
        .action {
          .action-sub {
            left: auto;
            right: 50%; } } } } }
  &.slider {
    [class^='col-'] {
      padding-left: 0;
      padding-right: 0; } }
  &.listview {
    &:not(:last-child) {
      margin-bottom: 32px; }
    .box-card {
      display: flex;
      flex-wrap: wrap;
      border-radius: 20px;
      padding: 10px;
      &:not(:last-child) {
        margin-bottom: 20px; }
      &__link {
        display: flex;
        flex-wrap: wrap;
        border-radius: 20px;
        color: $text;
        text-decoration: none;
        width: 100%; }
      &__img {
        width: 140px;
        height: 140px;
        border-radius: 14px;
        overflow: hidden;
        &.semisquare {
          width: 140px;
          height: 160px; }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover; } }
      &__content {
        flex: 0 0 calc(100% - 140px);
        max-width: calc(100% - 140px);
        padding-left: 24px;
        position: relative;
        .rating {
          position: absolute;
          top: 0;
          right: 0;
          bottom: auto; } }

      &__text {
        padding: 0;
        position: relative;
        .schedule {
          li {
            display: inline-block;
            margin-block-end: 0;
            & + li {
              margin-left: 20px; } } } }
      &__footer {
        display: flex;
        flex-wrap: wrap;
