  &-private {
    color: #47B4F1;
    background: rgba(71, 180, 241, 0.1); }
  &-success {
    color: #FFF;
    background: #307931; }
  &.nobg {
    background: transparent; }
  & + .nobg {
    margin-left: 16px; }
  &-proses {
    background: #DB3838;
    color: #FFF; } }

