// Static Page

.staticpage {
  // padding: 40px 0 100px
  padding: 71px 0 100px;
  min-height: 100vh;
  background: rgb(242, 242, 241); }

// Panel
.panel {
  padding: 32px 30px;
  border: 1px solid rgba(48, 121, 49, 0.2);
  background: #FFF;
  border-radius: 20px;
  &__heading {
    padding-bottom: 24px;
    border-bottom: 1px solid rgba(48, 121, 49, 0.2);
    h2 {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 0; } }
  &__content {
    padding: 24px 0;
    border-bottom: 0;
    & ~ .panel__content {
      border-top: 1px solid $border; } } }

// Pagination Box
.pagination-box {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  .pagination {
    margin-left: -10px;
    margin-right: -10px;
    margin-right: 24px;
    li {
      display: inline-block;
      vertical-align: middle;
      padding-left: 10px;
      padding-right: 10px;
      a {
        display: inline-block;
        vertical-align: middle;
        width: 27px;
        height: 27px;
        font-size: 14px;
        line-height: 27px;
        // background: #fbf1e6
        color: $primary;
        border-radius: 10px;
        text-align: center;
        text-decoration: none; }
      &.prev,
      &.next {
        a {
          text-align: center;
          background: transparent; }
        .svg {
          display: inline-block;
          width: 24px;
          height: 24px;
          margin: 0 auto; }
        &:hover {
          a {
            background: transparent; } } }
      &.next {
        .svg {
          transform: rotate(180deg); } }
      &:hover,
      &.active {
        a {
          background: $primary;
          color: #FFF; } } } } }
.listview + .pagination-box {
  margin-top: 50px; }


