/* ------------------------------------------------------------------------------------------------ */
/* Responsive */
/* ------------------------------------------------------------------------------------------------ */

main {
  padding-top: 64px;
  overflow-x: hidden;
  overflow-y: auto; }
p {
  font-size: 13px !important;
  line-height: 20px !important; }

.show-mobile {
  display: none;
  @include mobile {
    display: block; } }


// Mobile
@include mobile {
  // Auth
  .login-page {
    height: auto;
    .col-lg-5 {
      order: 2; }
    .col-lg-7 {
      order: 1; }
    &__image {
      position: relative;
      display: none;
      border-radius: 0 0 60px 0;
      overflow: hidden;
      &.mobile {
        display: block;
        width: calc(100% + 30px);
        max-height: 243px;
        margin-left: -15px;
        margin-right: -15px; } }
    &__wrap {
      padding: 48px 0;
      h2 {
        font-size: 24px; } } }

  // Homepage
  .home {
    &-banner {
      .col-md-7 {
        order: 2; }
      .col-md-5 {
        order: 1; }
      &__text {
        padding: 40px 0 60px;
        h2 {
          font-size: 30px;
          line-height: 1.5em; } }
      &__aksen {
        img {
          margin-left: 0;
          max-height: 333px;
          margin: 0 auto; } }
      .button-wrap {
        .btn {
          display: block;
          & + .btn {
            margin-left: 0;
            margin-top: 16px; } } } }
    .asclepio-go {
      [class^="aksen-"],
      [class^="aksen"] {
        display: none; } }
    .ongoing {
      .row:not(.justify-content-center) {
        [class^="col-"],
        [class*=" col-"] {
          max-width: 50%;
          flex: 0 0 50%; } }
      .aksen {
        bottom: 25% !important; }
      &__content {
        h4 {
          font-size: 16px;
          line-height: 1.5em; } }
      .tag-live {
        font-size: 12px; }
      h3 {
        max-width: 278px;
        margin: 24px auto 24px;
        text-align: center;
        font-size: 24px; }
      .author {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        .pp {
          width: 30px;
          height: 30px;
          display: inline-block;
          vertical-align: middle;
          margin-right: 10px; }
        span {
          font-size: 12px;
          line-height: 1.5em; } }
      .time {
        font-size: 12px;
        line-height: 1.5em; } }
    .testimoni {
      display: none; }
    .gallery-ig {
      .row {
        margin-left: -2px;
        margin-right: -2px;
        [class^="col-"],
        [class*=" col-"] {
          padding-left: 2px;
          padding-right: 2px;
          flex: 0 0 calc(100% / 3);
          max-width: calc(100% / 3); } } }
    &-faq {
      &__wrapper {
        .card-header {
          padding: 20px;
          padding-right: 0 !important;
          h5 {
            font-size: 13px !important;
            line-height: 20px !important;
            padding-right: 40px !important; } }
        .card-body {
          padding: 12px !important;
          border-radius: 16px !important;
          font-size: 13px !important;
          line-height: 20px !important; } } } }

  // Join webinar
  .join-webinar {
    border-radius: 100px 0 0 0;
    padding-bottom: 180px !important;
    .aksen-1 {
      top: auto;
      left: auto;
      right: -30px;
      bottom: 30px;
      transform: scale(.4); }
    .aksen-2 {
      top: auto;
      right: auto;
      bottom: -80px;
      left: -41px;
      transform: scale(.6);
      img {
        transform: scaleX(-1); } }
    .row {
      .col-md-7 {
        order: 2; }
      .col-md-5 {
        order: 1; } }
    &__img {
      .img_aksen {
        margin-top: 0;
        max-width: 235px;
        margin: 0 auto; } }
    &__text {
      h3 {
        font-size: 24px;
        line-height: 1.5em;
        margin-bottom: 32px; } }
    .btn-wrap {
      .btn {
        display: block;
        & + .btn {
          margin-top: 16px;
          margin-left: 0; } } } }
  .footer.minus-top {
    margin-top: -95px; }

  // Section
  .section {
    padding: 25px 0; }
  .section__heading {
    h3 {
      font-size: 20px;
      line-height: 1.5em; } }
  // Aksen
  .aksen {
    transform: scale(.4); }

  // Banner Small
  .banner {
    &__small {
      border-radius: 0 0 50px 0;
      &-content {
        max-width: 337px;
        margin: 0 auto;
        h2 {
          font-size: 30px;
          line-height: 1.5em; }
        p {
          font-size: 14px;
          line-height: 1.5em; } } } }


  // Wrap Box Card
  .wrap-box-card {
    .col-md-4 {
      width: 295px;
      max-width: 100%;
      .btn {
        display: none; } }
    .col-md-3 {
      width: 300px;
      max-width: 100%; }
    &.listview {
      .box-card {
        &__img {
          display: none; }
        &__content {
          padding-left: 0;
          flex: 0 0 100%;
          max-width: 100%; }
        &__footer {
          .right {
            width: 100%;
            padding-top: 24px; }
          .btn {
            display: block;
            width: 100%; } } } } }

  // Asclepedia & Asclepio Go
  .asclepedia,
  .asclepio-go {
    section.highlight {
      border-radius: 30px 0 0 0;
      .wrap-box-card {
        margin-left: -4px;
        margin-right: -4px;
        [class^="col-"],
        [class*=" col-"] {
          padding-left: 4px;
          padding-right: 4px; }
        .col-md-3 {
          flex: 0 0 50%;
          max-width: 50%;
          width: 50%; }
        .box-card {
          &__text {
            padding: 10px;
            h4 {
              display: block;
              display: -webkit-box;
              max-width: 100%;
              height: 43px;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              overflow: hidden;
              text-overflow: ellipsis;
              a {
                display: block; } }
            .author {
              .pp {
                margin-right: 8px; }
              span {
                font-size: 11px; } } }

          &__footer {
            padding: 10px;
            padding-top: 0; } } } }
    .wrap-box-card {
      justify-content: center;
      .col-md-4 {
        max-width: 100%;
        width: 100%; }
      .box-card {
        &__footer {
          .btn {
            min-width: auto;
            display: block; } } } }
    .jadwal {
      &__filter {
        display: none; }
      .box-calendar {
        margin-bottom: 20px; } } }

  // Asclepedia
  .asclepedia {
    .aksen {
      transform: scale(.6);
      &-1 {
        left: auto !important;
        right: -35px; }
      &-2 {
        right: auto !important;
        left: -70px;
        bottom: -50px !important; } } }

  // Asclepio Go
  .asclepio-go {
    .aksen {
      &-1 {
        bottom: auto !important;
        top: 0; } } }

  // Box Calendar
  .box-calendar {
    padding: 30px 20px;
    .weeks-wrapper {
      .week {
        &:not(:last-child) {
          margin-bottom: 30px; } } } }

  // Class Detail
  .class-detail {
    &__banner {
      border-radius: 0 0 50px 0;
      &-content {
        padding-left: 0;
        .class {
          &-type {
            margin-bottom: 16px;
            span {
              font-size: 13px; } }
          &-title {
            margin-bottom: 16px;
            h1 {
              font-size: 20px; } }
          &-pricerate {
            flex-wrap: wrap;
            align-items: center;
            flex-direction: row;
            margin-bottom: 24px; }
          &-rating,
          &-price {
            flex: 0 0 50%;
            max-width: 50%;
            margin-bottom: 0; }
          &-rating {
            order: 2; }
          &-price {
            order: 1;
            margin-left: auto;
            font-size: 16px;
            font-weight: 500; }
          &-btnwrap {
            .btn {
              display: block; } } } } }

    &__thumbnail {
      max-height: 225px;
      margin-bottom: 24px; }

    &__info {
      .detail-info {
        &__wrap {
          padding: 20px 10px;
          border-radius: 18px;
          .left {
            margin-bottom: 24px; }
          h4 {
            font-size: 14px;
            line-height: 1.5em; }
          p {
            font-size: 13px;
            line-height: 1.5em; }
          .timeplace {
            .ic {
              width: 24px;
              height: 24px; }
            span {
              font-size: 13px;
              line-height: 1.5em; } } } } }

    .rundown {
      &__item {
        padding: 20px 16px;
        padding-bottom: 60px;
        border-radius: 18px;
        position: relative;
        &-title {
          position: unset;
          h4 {
            font-size: 14px;
            line-height: 1.5em;
            margin-bottom: 12px; }
          .duration {
            top: auto;
            right: auto;
            left: 16px;
            bottom: 20px; } }
        &-text {
          font-size: 13px;
          line-height: 1.5em; } } } }

  // About
  .about {
    text-align: center;
    .aksen {
      &-1 {
        bottom: auto !important;
        top: 0;
        left: -70px !important; }
      &-2 {
        right: -40px !important; } }
    &__whatsthat {
      .section__heading {
        text-align: center !important; } }
    &__img {
      max-width: 300px;
      margin: 0 auto 40px;
      .row {
        margin-left: -5px;
        margin-right: -5px;
        [class^="col-"],
        [class*=" col-"] {
          padding-left: -5px !important;
          padding-left: -5px !important;
          width: 50%;
          flex: 0 0 50%; } } }
    &__card {
      text-align: center;
      margin-bottom: 16px;
      .icon {
        margin: 0 auto 20px; }
      .text {
        h4 {
          font-size: 16px;
          line-height: 1.5em;
          margin-bottom: 16px; }
        p {
          font-size: 13px;
          line-height: 1.5em; } } } }

  .page-profile {
    padding: 40px 0 60px;
    &__informasi {
      margin-bottom: 32px;
      .section__heading {
        display: none; } } }




  // Kelas yg Diikuti
  .following-class {
    padding-top: 40px;
    .page_title {
      margin-bottom: 24px;
      h2 {
        font-size: 20px;
        line-height: 1.5em; } } }

  // Benefits
  .benefits {
    .page_title {
      margin-bottom: 24px;
      display: block;
      h2 {
        font-size: 20px;
        line-height: 1.5em;
        margin-bottom: 20px; }
      .search-box {
        width: 100%; } }

    &__panel {
      padding: 20px;
      .wrap-box-card {
        margin-bottom: 24px;
        .box-card {
          padding: 0;
          border: none;
          margin-bottom: 0;
          &__text {
            h4 {
              margin-bottom: 0; } } } } } }
  .benefits__item + .pagination-box {
    margin-top: 32px; }

  // Pagination
  .pagination-box {
    margin-top: 32px; }

  // Static Page
  .staticpage {
    padding: 40px 0;
    padding-top: 70px; }

  // Page Freebox
  .page-freebox {
    .page_title {
      margin-bottom: 24px;
      display: block;
      h2 {
        font-size: 20px;
        line-height: 1.5em;
        margin-bottom: 20px; } } }

  // Pembayaran
  .pembayaran {
    &__waiting {
      .countdown {
        margin-bottom: 20px;
        &__time {
          font-size: 20px !important;
          line-height: 1.5em !important; } }
      .expired {
        span {
          text-align: left !important; } } }

    .panel {
      padding: 20px;
      padding-top: 40px; }
    &.success {
      .message-info {
        .row {
          .col,
          .col-4 {
            flex: 0 0 100%;
            max-width: 100%;
            margin-bottom: 24px; } } } } }

  // page-topik
  .page-topik {
    &__title {
      margin-bottom: 32px;
      h1 {
        font-size: 24px;
        line-height: 1.5em;
        margin-bottom: 32px; } }
    &__list {
      margin-left: -6px;
      margin-right: -6px;
      justify-content: flex-start;
      .topik-item {
        padding-left: 6px;
        padding-right: 6px; } }
    &__bottom {
      .notes {
        text-align: center; }
      .btn-wrap {
        width: 100%;
        padding-top: 32px;
        text-align: right; } } }

  // Page Kuisioner
  .page-kuisioner {
    &__question {
      &:not(:last-child) {}
      &.with-thumbnail {
        margin-bottom: 40px;
        .page-kuisioner__choice {
          .col-md-3 {
            flex: 0 0 50%;
            max-width: 50%;
            margin-bottom: 32px; } } }
      &.with-column {
        .page-kuisioner__choice {
          .col-md-3 {
            margin-bottom: 24px; }
          .custom-control-label {
            text-align: left; } } }
      &.with-txtarea {
        margin-bottom: 52px;
        .form-control {
          min-height: 210px; } } } }

  // Faq
  .faq {
    .banner__small {
      .aksen {
        &-1 {
          bottom: auto !important;
          top: 0;
          left: -55px !important; }
        &-2 {
          transform: scale(.7);
          left: calc(50% - 100px) !important; }
        &-3 {
          transform: scale(.6);
          right: -30px !important; } } } } }
