// Topik page
.page-topik {
  padding: 50px 0;
  .col-md-8 {
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 180px); }
  &__title {
    margin-bottom: 72px;
    text-align: center;
    h1 {
      font-size: 34px;
      line-height: 1.5em;
      font-weight: 700;
      margin-bottom: 30px; }
    p {
      font-size: 20px;
      line-height: 1.5em;
      font-weight: 700;
      color: $grey; } }
  &__list {
    margin-left: -8px;
    margin-right: -8px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .topik-item {
    padding-left: 8px;
    padding-right: 8px;
    margin-bottom: 20px;
    cursor: pointer;
    span {
      display: inline-block;
      vertical-align: middle;
      padding: 16px;
      border: 1px solid #E7E9F0;
      border-radius: 10px;
      font-size: 14px;
      line-height: 22px;
      font-weight: 500;
      color: $text; }
    .custom-checkbox {
      padding-left: 0;
      .custom-control-label {
        padding: 16px;
        border-radius: 10px;
        border: 1px solid #E7E9F0;
        display: block;
        cursor: pointer;
        &:before,
        &:after {
          opacity: 0;
          visibility: hidden; } } }
    .custom-control .custom-control-input:checked ~ .custom-control-label {
      background: rgba(117, 178, 72, 0.2);
      border-color: #E7E9F0;
      color: #75B248; }
    &.selected {
      span {
        border-color: rgba(117, 178, 72, 0.1);
        background: rgba(117, 178, 72, 0.1);
        color: #75B248; } } }
  &__bottom {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
    .notes {
      p {
        color: $grey;
        font-size: 14px;
        line-height: 1.5em; } }
    .btn-wrap {
      margin-top: 0; } }

  &.small {
    padding: 0;
    .heading {
      margin-bottom: 15px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      label {
        font-size: 16px;
        font-weight: 600; }
      span {
        font-size: 14px;
        color: #BDBFBD; } }
    .page-topik__list {
      margin-left: -5px;
      margin-right: -5px;
      justify-content: flex-start; }
    .topik-item {
      padding-left: 5px;
      padding-right: 5px;
      margin-bottom: 10px;
      .custom-control .custom-control-input:checked ~ .custom-control-label {
        background: rgba(117, 178, 72, 0.1);
        border-color: rgba(117, 178, 72, 0.1);
        color: $text; } }
    .custom-checkbox {
      .custom-control-label {
        padding: 10px 16px; } } } }

