// Join Webiner
.join-webinar {
  padding: 115px 0 100px;
  background: #75B248;
  border-radius: 150px 0 0 0;
  margin-top: 110px;
  position: relative;
  overflow: visible;
  .aksen-1 {
    position: absolute;
    top: 260px;
    left: 0; }
  .aksen-2 {
    position: absolute;
    top: 83px;
    right: 0; }

  &__text {
    h3 {
      font-size: 34px;
      line-height: 41px;
      font-weight: 700;
      color: #FFF;
      margin-bottom: 60px; }
    .btn-wrap {
      .btn {
        font-weight: 500;
        min-width: 200px; }
      .btn + .btn {
        margin-left: 24px; }
      .btn-primary {
        color: #75B248;
        background: #fff;
        border-color: #FFF; }
      .btn-border {
        border-color: #fff;
        color: #FFF;
        &:hover {
          color: #75B248;
          background: #FFF; } } } }
  &__img {
    position: relative;
    .img_aksen {
      margin-top: -200px; } } }

// Kelas yang diikuti dan Benefits
.following-class,
.benefits {
  padding: 70px 0;
  background: rgb(242, 242, 241);
  .page_title {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
    h2 {
      margin-bottom: 0; } } }

.benefits {
  &__item {
    margin-bottom: 20px; }
  &__head {
    padding-bottom: 16px;
    border-block-end: 1px solid $border;
    margin-bottom: 24px;
    &.listview {
      .box-card {
        &__img {
          width: 78px;
          height: 78px;
          border-radius: 14px;
          overflow: hidden; }
        &__content {
          flex: 0 0 calc(100% - 78px);
          max-width: calc(100% - 78px); } } } }
  &__links {
    padding-bottom: 24px; }
  &__item + .pagination-box {
    margin-top: 85px; } }

// Cart
.cart,
.pembayaran {
  .panel {
    margin-bottom: 20px;
    &.voucher {
      padding: 20px; } }
  .voucher {
    .form-group {
      position: relative;
      margin-bottom: 0;
      label {
        font-size: 16px;
        line-height: 1.5em;
        font-weight: 600;
        margin-bottom: 20px; }
      .form-control {
        padding-right: 90px; }
      .btn-link {
        padding: 0;
        position: absolute;
        bottom: 15px;
        right: 16px;
        font-size: 14px;
        line-height: 20px;
        text-decoration: none; } }
    &__used {}
    position: relative;
    p {
      font-size: 14px;
      max-width: 214px;
      margin-bottom: 0; }
    .remove {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 20px;
      height: 20px; } }
  &__total {
    h3 {
      font-size: 20px;
      margin-bottom: 32px; }
    .total {
      &__detail {
        font-size: 14px;
        line-height: 1.5em;
        li {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 16px;
          .left {
            font-weight: 500; }
          &.grandtotal {
            font-weight: 500; }
          &.discount {
            color: $secondary; } } } } } }

// Pembayaran
.pembayaran {
  &__invoice {
    .content {
      text-align: center;
      margin-bottom: 24px; }
    .icon {
      width: 170px;
      height: 110px;
      margin: 0 auto 12px; }
    p {
      font-size: 14px;
      color: $text-light; } }

  &__method {
    p {
      font-size: 14px;
      color: $text-light;
      margin-bottom: 22px; }
    &-item {
      padding-bottom: 32px;
      &:not(:last-child) {
        border-bottom: 1px solid $border;
        margin-bottom: 32px; } }
    &-form {}
    .custom-control {
      margin-bottom: 20px;
      &-label {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; }
      &:last-child {
        margin-bottom: 0; } } }

  &__waiting {
    .page_title {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-bottom: 32px;
      .countdown {
        &__time {
          font-size: 34px;
          line-height: 41px;
          font-weight: 700; } }
      .expired {
        p {
          font-weight: 600;
          color: $text-light;
          margin-bottom: 10px; }
        span {
          display: block;
          font-size: 14px;
          color: $text;
          text-align: right;
          font-weight: 600; } } }
    .panel {
      margin-bottom: 32px; } }
  &__selected {
    padding: 14px 30px;
    background: rgba(48, 121, 49, 0.04);
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-block-end: 24px;
    label {
      font-weight: 600; } }
  &__account {
    &-item {
      text-align: center;
      &:not(:last-child) {
        margin-bottom: 30px; }
      label {
        font-size: 14px;
        font-weight: 500; }
      .account_no,
      .total {
        font-weight: 700;
        font-size: 20px;
        line-height: 1.5em; }
      .copy {
        display: inline-block;
        margin: 12px auto 0;
        font-size: 14px;
        color: $text-light;
        text-decoration: none;
        .ic {
          width: 16px;
          height: 16px;
          display: inline-block;
          vertical-align: middle;
          margin-right: 5px; } } } }

  &.success {
    .content {
      width: 100%;
      max-width: 562px;
      margin: 0 auto 32px;
      text-align: center;
      a {
        font-weight: 600;
        color: $text; } }
    .notif-box {
      margin-bottom: 32px; }
    .not-recived-email {
      text-align: center;
      margin-bottom: 32px;
      span {
        color: #838683;
        font-size: 14px;
        line-height: 1.2em;
        font-weight: 500; }
      .btn-link {
        padding: 0;
        padding-left: 16px;
        font-weight: 600; } }
    .message-info {
      margin-bottom: 32px;
      h4 {
        font-size: 14px;
        line-height: 22px;
        font-weight: 500;
        margin-bottom: 22px;
        color: #838683; }
      .ic-label {
        .ic {
          width: 18px;
          height: 18px;
          display: inline-block;
          vertical-align: middle; }
        label {
          font-size: 12px;
          line-height: 1.5em;
          color: #838683; } }
      b {
        padding-left: 22px;
        font-size: 14px;
        line-height: 1.5em;
        font-weight: 500;
        color: $text; }

      .col-4 {
        .ic-label {
          .ic {
            width: 18px;
            height: 18px; } }
        b {
          display: inline-block;
          padding-left: 0; }
        .ic {
          width: auto;
          height: 22px;
          display: inline-block;
          vertical-align: middle;
          margin-right: 8px;
          img {
            height: 100%; } } } }
    .webinar {
      h4 {
        color: #838683;
        margin-bottom: 14px;
        font-size: 14px;
        line-height: 22px;
        font-weight: 500; }
      .listview {
        .box-card {
          padding: 24px;
          &__content {
            flex: 0 0 100%;
            max-width: 100%;
            padding-left: 0;
            h4 {
              color: $text; }
            .price {
              color: $text; } } } } } } }

