/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

header.header {
  padding: 12px 0;
  background: $background;
  max-height: 80px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  .row {
    align-items: center;
    justify-content: space-between; }
  .header {
    &__logo {
      display: inline-block;
      vertical-align: middle;
      max-height: 54px;
      width: auto;
      margin-right: 70px;
      img {
        max-width: 100%;
        max-height: 54px; } }
    &__right {
      text-align: right;
      .btn {
        min-width: 143px;
        & + .btn {
          margin-left: 20px; } } }

    &__menu {
      display: inline-block;
      vertical-align: middle;
      ul {
        margin-left: -16px;
        margin-right: -16pxx;
        li {
          padding-left: 16px;
          padding-right: 16px;
          display: inline-block;
          cursor: pointer;
          a {
            font-size: 14px;
            line-height: 22px;
            color: #202320;
            cursor: pointer;
            text-decoration: none;
            font-weight: 500; }
          &.current-menu-item,
          &:hover {
            a {
              color: $primary; } } } } }
    &.user-login {
      .header__right {
        display: flex;
        justify-content: flex-end; } } }

  .box-navmobile {
    display: none;
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    max-width: 330px;
    height: 100vh;
    overflow-y: auto;
    padding: 18px 20px;
    background: #FFF;
    z-index: 101;
    transition: all .3s linear;
    &__close {
      width: 28px;
      height: 28px;
      margin-bottom: 24px; }
    .btn {
      display: block;
      & + .btn {
        margin-top: 16px; } }
    hr {
      margin: 20px 0; }
    .header__menu-mobile {
      ul {
        li {
          a {
            font-size: 14px;
            line-height: 1.5em;
            font-weight: 500;
            color: $text;
            padding: 15px 20px;
            display: block;
            border-radius: 10px;
            text-decoration: none; }
          hr {
            margin: 10px 0; }
          &.current-menu-item {
            a {
              background: #FAFBF1;
              color: $primary;
              font-weight: 600; } }
          &.logout {
            a {
              color: #E22424; } }
          u.count {
            text-align: center;
            text-decoration: none;
            border-radius: 50%;
            width: 20px;
            height: 20px;
            background: #E72D2D;
            color: #FFF;
            font-size: 12px;
            line-height: 20px;
            display: inline-block;
            vertical-align: middle;
            margin-left: 10px; } } } } }

  .icon-menu {
    position: absolute;
    width: 28px;
    height: 28px;
    top: 18px;
    left: 15px;
    z-index: 4;
    display: none;
    transition: all .3s linear;
    span {
      display: block;
      width: 100%;
      height: 2px;
      position: absolute;
      opacity: 1;
      right: 0;
      background: $text;
      transition: all .3s linear;
      &:nth-child(1) {
        top: 8px; }
      &:nth-child(2) {
        top: 18px; }
      &:nth-child(3) {
        top: 28px; } } } }

.account-user {
  position: relative;
  // max-width: 146px
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-right: 32px;
  cursor: pointer;
  &:after {
    content: "";
    width: 20px;
    height: 20px;
    background-image: url('../images/ic-chevron-down.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 6px;
    right: 0;
    transition: all .3s ease; }
  &.mobile {
    &:after {
      display: none; } }

  .account {
    &-photo {
      width: 32px;
      height: 32px;
      border-radius: 12px;
      margin-right: 16px;
      img {
        width: 100%;
        height: 100%; } }
    &-name {
      font-size: 14px;
      line-height: 1.3em;
      color: $text;
      font-weight: 500; } }
  .account-submenu {
    position: absolute;
    min-width: 250px;
    top: calc(100% + 40px);
    right: 0;
    left: 0;
    margin: 0 auto;
    padding: 20px;
    border-radius: 6px;
    background: #fff;
    text-align: left;
    transition: all .5s ease;
    box-shadow: 0px 20px 44px rgba(0, 0, 0, 0.1);
    opacity: 0;
    visibility: hidden;
    &:before {
      content: "";
      position: absolute;
      top: -16px;
      left: calc(50% - 16px);
      @include triangle('up',32px,16px,#FFF); }
    ul {
      li {
        &:not(:last-child) {
          margin-block-end: 20px; }
        a {
          font-size: 14px;
          line-height: 1.5em;
          color: $text;
          text-decoration: none; }
        u.count {
          text-align: center;
          text-decoration: none;
          border-radius: 50%;
          width: 20px;
          height: 20px;
          background: #E72D2D;
          color: #FFF;
          font-size: 12px;
          line-height: 20px;
          display: inline-block;
          vertical-align: middle;
          margin-left: 10px; }
        &.logout {
          a {
            color: #E72D2D; } } } } } }



body.usracnt-open {
  .account-user {
    &:after {
      transform: rotate(-180deg); }
    .account-submenu {
      opacity: 1;
      visibility: visible;
      top: calc(100% + 20px); } } }

// Header auth
header.header-auth {
  padding: 12px 0;
  background: #FFF;
  max-height: 80px;
  .row {
    align-items: center; }

  .header {
    &__logo {
      display: block;
      max-height: 54px;
      width: auto;
      img {
        max-width: 100%;
        max-height: 54px; } }
    &__right {
      text-align: right;
      .play-music {
        display: inline-block;
        vertical-align: middle;
        margin-right: 30px;
        span {
          font-size: 14px;
          line-height: 1.5em; } }
      .language {
        display: inline-block;
        vertical-align: middle;
        a {
          display: inline-block;
          vertical-align: middle;
          color: $text-light;
          text-decoration: none;
          &.active {
            color: $text; } }
        span {
          display: inline-block;
          vertical-align: middle;
          width: 1px;
          height: 17px;
          background: #313343;
          margin: 0 10px; } } } } }

.switch {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 14px;
  margin-left: 16px;
  input {
    opacity: 0;
    width: 0;
    height: 0; }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #BDBFBD;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 20px;
    border: 1px solid #BDBFBD;
    &:before {
      position: absolute;
      content: "";
      height: 20px;
      width: 20px;
      left: -4px;
      bottom: -4px;
      background: #FFF;
      border: 2px solid #BDBFBD;
      border-radius: 50%;
      -webkit-transition: .4s;
      transition: .4s; } }
  input {
    &:checked + .slider {
      background: #307931; }
    &:focus + .slider {
      box-shadow: 0 0 1px #2196F3; }
    &:checked + .slider:before {
      -webkit-transform: translateX(33px);
      -ms-transform: translateX(33px);
      transform: translateX(33px);
      border-color: #307931; } } }



// Mobile
@include mobile {
  header.header {
    [class^="col-"],
    [class*=" col-"] {
      width: auto;
      flex: 0 0 auto; }
    .icon-menu,
    .box-navmobile {
      display: block; }
    .header {
      &__logo {
        padding-left: 60px;
        height: 44px;
        margin-right: 0;
        img {
          max-height: 44px; } }
      &__menu {
        display: none; }
      &__right {
        // display: none
        .aksen {
          display: none; }
        .btn {
          min-width: 120px; }
        .btn-primary,
        .account-user {
          display: none; } } } }
  header.header-auth {
    .header {
      &__logo {
        padding-left: 0; } } }
  body.mobile-menu-open {
    height: 100vh;
    overflow: hidden;

    header.header {
      &:before {
        content: "";
        width: 100%;
        height: 100%;
        background: #202320;
        opacity: .5;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 101; } }
    .box-navmobile {
      left: 0; } } }
