/* ------------------------------------------------------------------------------------------------ */
/* Footer */
/* ------------------------------------------------------------------------------------------------ */
.footer {
  background: #202320;
  padding-top: 60px;
  padding-bottom: 60px;
  border-radius: 150px 0 0 0;
  position: relative;
  z-index: 10;
  &.minus-top {
    margin-top: -185px; }
  &__logo {
    margin-bottom: 20px;
    display: block;
    width: auto;
    height: 54px;
    span {} }
  &__cpyright {
    font-size: 14px;
    line-height: 22px;
    color: #BDBFBD;
    white-space: nowrap; }
  &__nav-wrap {
    padding-top: 50px;
    display: flex;
    flex-wrap: wrap;
    color: #fff;
    justify-content: space-between; }
  &__menu {
    h4 {
      font-size: 16px;
      line-height: 20px;
      font-weight: 700;
      margin-bottom: 15px;
      color: #fff; }
    ul {
      li {
        margin-bottom: 12px;
        a {
          color: #BDBFBD; } } } }
  &__socmed {
    padding-top: 30px;
    margin-bottom: 40px;
    padding-left: 24px;
    .icon_socmed {
      display: inline-block;
      vertical-align: middle;
      width: 42px;
      height: 42px;
      border-radius: 50%;
      & + .icon_socmed {
        margin-left: 13px; }
      &:first-child {
        margin-left: 0; }
      img {
        display: block;
        width: 100%;
        height: 100%; } } }
  &__subscribes {
    padding-left: 24px;
    span {
      color: #fff;
      font-size: 16px;
      line-height: 20px;
      display: block;
      margin-bottom: 24px; }
    .form-group {
      position: relative; }
    .form-control {
      background: #2D302D;
      border-color: #2D302D;
      border-radius: 10px; }
    .btn {
      position: absolute;
      padding: 8px 12px;
      top: 8px;
      right: 5px;
      border-radius: 10px; } }
  .language {
    margin-top: auto;
    .ic {
      width: 24px;
      height: 24px;
      display: inline-block;
      vertical-align: middle;
      margin-right: 10px;
      img {
        width: 100%;
        height: 100%; } } }
  .sel-language {
    .btn {
      width: auto;
      background: transparent;
      color: #FFF;
      border: 0 !important;
      padding-top: 0 !important;
      padding-bottom: 0 !important;
      padding-left: 0 !important; } } }


// Mobile
@include mobile {
  .footer {
    padding-bottom: 33px;
    border-radius: 100px 0 0 0;
    .container {
      padding-left: 20px;
      padding-right: 20px; }
    &__logo {
      margin-bottom: 24px; }
    &__nav {
      &-wrap {
        padding-top: 40px; } }

    &__menu {
      flex: 0 0 50%;
      max-width: 50%; }
    &__socmed,
    &__subscribes {
      padding-left: 0; }
    .language {
      &:not(.show-mobile) {
        display: none; } }
    &__subscribes {
      margin-bottom: 24px;
      span {
        font-size: 13px; } }
    &__cpyright {
      text-align: center;
      color: #BDBFBD; } } }
